ext-align: right !important\n      \n    .text-{size}-justify\n      text-align: justify !important","html, body\n  height: 100%\n  min-height: 100%\n  position: relative\n\nhtml\n  font-size: $font-size-root\n  text-rendering: optimizeLegibility\n  -webkit-font-smoothing: antialiased\n  -moz-osx-font-smoothing: grayscale\n  -webkit-tap-highlight-color: rgba(0, 0, 0, 0)\n  overflow-x: hidden\n\nbody\n  font-family: $body-font-family\n  line-height: $line-height-root\n\nheader\n  transition: .3s padding $transition.swing\n  width: 100%\n  z-index: 1\n\nmain\n  transition: .3s padding $transition.swing\n  will-change: padding-left\n\na\n  color: $theme.primary\n\n::-ms-clear\n::-ms-reveal\n  display: none\n","for heading, options in $headings\n  {heading}\n    color: $heading-color\n    font-size: options.size\n    font-weight: options.weight\n    line-height: options.line-height\n    letter-spacing: options.letter-spacing\n    margin-bottom: $spacers.three.y\n    \n    @media screen and (max-width: $grid-breakpoints.sm)\n      font-size: options.size * .6","ul, ol\n  padding-left: $spacers.four.x",".display-4\n  font-size: $headings.h1.size\n  font-weight: $headings.h1.weight\n  line-height: $headings.h1.line-height\n  letter-spacing: $headings.h1.letter-spacing\n  \n.display-3\n  font-size: $headings.h2.size\n  font-weight: $headings.h2.weight\n  line-height: $headings.h2.line-height\n  letter-spacing: $headings.h2.letter-spacing\n  \n.display-2\n  font-size: $headings.h3.size\n  font-weight: $headings.h3.weight\n  line-height: $headings.h3.line-height\n  letter-spacing: $headings.h3.letter-spacing\n  \n.display-1\n  font-size: $headings.h4.size\n  font-weight: $headings.h4.weight\n  line-height: $headings.h4.line-height\n  letter-spacing: $headings.h4.letter-spacing\n  \n.headline\n  font-size: $headings.h5.size\n  font-weight: $headings.h5.weight\n  line-height: $headings.h5.line-height\n  letter-spacing: $headings.h5.letter-spacing\n  \n.title\n  font-size: $headings.h6.size\n  font-weight: $headings.h6.weight\n  line-height: $headings.h6.line-height\n  letter-spacing: $headings.h6.letter-spacing\n  \n.subheading\n  font-size: $headings.subheading.size\n  font-weight: $headings.subheading.weight\n  \n.body-2\n  font-size: $headings.body-2.size\n  font-weight: $headings.body-2.weight\n  \n.body-1\n  font-size: $headings.body-1.size\n  font-weight: $headings.body-1.weight\n  \n.caption\n  font-size: $headings.caption.size\n  font-weight: $headings.caption.weight\n  \np\n  margin-bottom: $spacers.three.y",".alert\n  border-radius: $alert-border-radius\n  border-width: $alert-border-width\n  border-style: solid\n  border-color: rgba(#000, .8)\n  color: $alert-color\n  display: flex\n  font-size: $alert-font-size\n  text-align: left\n  padding: $alert-padding\n  position: relative\n  margin: $alert-margin\n  \n  &__icon.icon, &__dismissible .icon\n    align-self: center\n    color: $alert-icon-color\n    font-size: $alert-icon-font-size\n    \n  &__icon\n    margin-right: $alert-padding\n    \n  &__dismissible\n    margin-right: 0\n    margin-left: $alert-padding\n    transition: $primary-transition\n    align-self: flex-start\n    text-decoration: none\n    \n    &:hover\n      color: lighten($alert-icon-color, 10%)\n    \n  > div\n    display: flex\n    align-items: center\n    width: 100%\n  \n  &--primary\n    alert($theme.primary)\n\n  &--secondary\n    alert($theme.primary)\n\n  &--info\n    alert($theme.info)\n        \n  &--error\n    alert($theme.error)\n\n  &--success\n    alert($theme.success)\n    \n  &--warning\n    alert($theme.warning)\n    \n  &--no-icon\n    .alert__icon\n      display: none\n    \n  @media screen and (max-width: $grid-breakpoints.sm)\n    &__icon\n      display: none","alert(primary)\n  color: #fff\n  background-color: primary\n  border-color: rgba(#000, 0.2)",".app\n  &__bar\n    align-items: center\n    background: rgba($material-theme.fg-color, $material-theme.divider-percent)\n    display: flex\n    width: 100%\n\n    .btn-dropdown--overflow .input-group\n      border: none\n      margin: 0\n\n    .input-group__details\n      display: none\n\n    > div\n      min-width: 130px\n      &:not(:first-child)\n        margin-left: 1px\n\n        &:before\n          content: ''\n          position: absolute\n          height: 60%\n          top: 50%\n          transform: translateY(-50%)\n          left: -1px\n          background: $material-twelve-percent-dark\n          width: 1px\n          z-index: 0\n\n    .input-group:not(.input-group--focused)\n      .input-group__input\n        &:hover\n          background: $grey.lighten-4\n\n    .btn-toggle\n      box-shadow: none\n      background: transparent\n      position: relative\n      padding: 0 8px\n\n      .btn\n        background: transparent\n        border: none !important\n        height: 42px\n        margin: 0 8px\n\n        .icon\n          font-size: 26px\n          width: 26px\n",".avatar\n  display: flex\n  justify-content: center\n  align-items: center\n  text-align: center\n\n  img, .icon\n    height: 42px\n    width: 42px\n    border-radius: 50%",".badge\n  position: relative\n  \n  &:after\n    color: $badge-color\n    content: attr(data-badge)\n    display: flex\n    position: absolute\n    font-family: $badge-font-family\n    top: -($badge-height / 2)\n    right: -($badge-width)\n    background-color: $theme.primary\n    border-radius: $badge-border-radius\n    height: $badge-height\n    width: $badge-width\n    font-size: $badge-font-size\n    justify-content: center\n    align-items: center\n    flex-direction: row\n    flex-wrap: wrap\n  \n  &--overlap\n    &:after\n      top: -8px\n\n    &.badge--left\n      &:after\n        left: -8px\n        right: initial\n        \n    &:after\n      right: -8px\n      \n  &--icon\n    &:after\n      font-family: 'Material Icons'\n      \n  &--left\n    &:after\n      left: -($badge-width)",".bottom-nav\n  background: $theme.primary\n  bottom: 0\n  box-shadow: 0 3px 14px 2px rgba(#000, .12)\n  display: flex\n  height: 56px\n  justify-content: center\n  position: fixed\n  transform: translate3d(0, 60px, 0)\n  transition: all .4s cubic-bezier(.25, .8, .50, 1)\n  width: 100%\n  z-index: 4\n\n  &--absolute\n    position: absolute\n\n  &--active\n    transform: translate3d(0, 0, 0)\n\n  .btn\n    border-radius: 0\n    flex: 1 1 32px\n    height: 100%\n    margin: 0\n    max-width: 168px\n    min-width: 80px\n    padding: 0px 12px\n    opacity: .5\n    text-transform: capitalize\n    transform-origin: 50% 50%\n\n    .icon\n      color: inherit\n      transition: all .4s cubic-bezier(.25, .8, .50, 1)\n\n    .btn__content\n      flex-direction: column-reverse\n      height: 56px\n      font-size: 12px\n      transform: scale3d(1, 1, 1) translate3d(0, 1px, 0)\n      white-space: nowrap\n      will-change: font-size\n\n    &--active\n      opacity: 1\n\n      .btn__content\n        font-size: 14px\n        transform: scale3d(1, 1, 1) translate3d(0, 0, 0)\n\n    &:not(.btn--active)\n      filter: grayscale(100%)\n\n  &--shift\n    .btn__content\n      font-size: 14px\n\n      span\n        height: 21px\n\n    .btn\n      transition: all .3s\n      min-width: 56px\n      max-width: 96px\n\n      &--active\n        min-width: 96px\n        max-width: 168px\n        flex: 1 1 72px\n\n        .btn__content\n          transform: scale3d(1, 1, 1) translate3d(0, 2px, 0)\n\n.bottom-nav--shift\n  .btn:not(.btn--active) .btn__content\n    transform: scale3d(1, 1, 1) translate3d(0, 10px, 0)\n\n    span\n      color: transparent\n",".breadcrumbs\n  display: flex\n  justify-content: center\n  flex-wrap: wrap\n  flex: $breadcrumbs-flex\n  margin: $breadcrumbs-margin\n  list-style-type: none\n  \n  li\n    &:not(:last-child):after\n      color: $grey.lighten-1\n      content: attr(data-divider)\n      vertical-align: middle\n      \n    &:last-child a\n      color: $grey.lighten-1\n      pointer-events: none\n      cursor: default\n      \n  &--with-icons\n    li\n      &:not(:last-child):after\n        font-family: 'Material Icons'\n      \n        \n  &__item\n    align-items: center\n    color: $theme.primary\n    display: inline-flex\n    font-size: $breadcrumbs-item-font-size\n    padding: $breadcrumbs-padding\n    height: $breadcrumbs-height\n    text-decoration: none\n    line-height: $breadcrumbs-height\n    transition: $primary-transition\n    \n    &:hover\n      color: $grey.darken-1\n      \n    &--disabled\n      color: $grey.lighten-2\n      pointer-events: none",".btn-dropdown\n  display: block\n  position: relative\n\n  input\n    text-align: left\n    border-right: 1px solid transparent\n    transition: border-right .3s $transition.ease-in-out\n\n  .input-group--focused\n    input + .icon\n      transform: rotate(-180deg)\n\n  .menu, .menu__activator\n    width: 100%\n\n  .menu__content\n    border-top-left-radius: 0\n    border-top-right-radius: 0\n\n  &--overflow, &--editable, &--segmented\n    .input-group\n      input, label\n        height: 40px\n        line-height: 40px\n\n    input, label\n      padding-left: 16px\n\n    .input-group--focused .input-group__input\n      background-color: $material-theme.bg-color\n      elevation(8)\n\n    .input-group__input\n      transition: .3s $transition.ease-in-out\n\n      &:hover\n        background-color: $material-theme.bg-color\n\n    .input-group__details\n      height: 0\n      min-height: 0\n      padding: 0\n\n      &:after\n        display: none\n\n    .input-group__hint\n      display: none\n\n  &--light\n    btn-dropdown-theme($material-twelve-percent-dark)\n\n  &--dark\n    btn-dropdown-theme($material-twelve-percent-light)\n","btn-dropdown-theme(theme)\n  &.btn-dropdown--segmented\n    input\n      border-right-color: theme\n      \n  &.btn-dropdown--editable\n    .input-group--focused\n      input\n        border-right-color: theme\n  \n  &.btn-dropdown--overflow, &.btn-dropdown--editable, &.btn-dropdown--segmented\n    .input-group\n      border-top: 1px solid theme\n      \n  .input-group__hint:after\n    display: none",".btn-toggle\n  display: inline-flex\n  border-radius: 2px\n  transition: $primary-transition\n  will-change: background, box-shadow\n\n  .btn\n    color: $color-theme\n    justify-content: center\n    min-width: auto\n    padding: 0 8px\n    margin: 0\n    opacity: .4\n    border-radius: 0\n\n    &:not(:last-child)\n      border-right: 1px solid transparent\n\n    &:after\n      display: none\n\n    &[data-selected]\n      opacity: 1\n      background: rgba($material-theme.fg-color, $material-theme.divider-percent)\n\n      &:not(:last-child):not([data-only-child])\n        border-right-color: rgba($material-theme.fg-color, $material-theme.divider-percent)\n\n    .icon\n      font-size: 30px\n\n    span + .icon\n      font-size: initial\n      margin-left: 10px\n\n  &--selected\n    background: $material-theme.bg-color\n    elevation(2)\n","/** Base Spec */\n.btn\n  align-items: center\n  background: $grey.lighten-2\n  border-radius: $button-border-radius\n  color: $button-color\n  display: inline-flex\n  height: $button-height\n  font-size: $button-font-size\n  font-weight: $button-font-weight\n  justify-content: center\n  margin: $button-margin\n  min-width: $button-min-width\n  outline: 0\n  padding: $button-padding\n  text-transform: $button-text-transform\n  text-decoration: none\n  transition: $primary-transition\n  position: relative\n  vertical-align: middle\n\n\n/** Psuedo */\n.btn\n  &:after\n    border-radius: inherit\n    bottom: 0\n    content: ''\n    left: 0\n    position: absolute\n    top: 0\n    right: 0\n    transition: $primary-transition\n\n  &.btn--disabled\n    box-shadow: none !important\n    pointer-events: none\n    opacity: .4\n\n    &:not(.btn--loader)\n      color: $button-dark-disabled-color\n\n\n/** Content */\n.btn\n  &__content\n    align-items: center\n    display: flex\n    flex: 1 0 auto\n    justify-content: center\n    transition: $primary-transition\n    white-space: nowrap\n    overflow: hidden\n    text-overflow: ellipsis\n\n\n/** Types */\n.btn\n  &--flat\n    background-color: transparent !important\n    box-shadow: none !important\n\n    &.btn--light\n      &:hover:after\n        background-color: $button-light-overlay\n\n      &.btn--disabled\n        color: $button-light-disabled-color\n\n    &.btn--dark\n      &:hover:after\n        background-color: $button-dark-overlay\n\n      &.btn--disabled\n        color: $button-dark-disabled-color\n\n  &--raised\n    will-change: box-shadow\n    elevation(2)\n\n    &:active\n      elevation(8)\n\n  &--icon\n    background: transparent\n    box-shadow: none !important\n    border-radius: 50%\n    justify-content: center\n    height: $button-height\n    width: $button-height\n    min-width: 0\n    padding: 0\n    \n    .icon\n      color: inherit\n\n    &:after\n      border-radius: 50%\n      opacity: .12\n\n    &:hover\n      border-radius: 50%\n\n      &:after\n        background-color: currentColor\n        opacity: .12\n\n    &.btn--disabled\n      background-color: transparent !important\n      color: $button-light-disabled-color !important\n      pointer-events: none\n      \n    &.btn--small\n      width: $button-small-height\n\n    &.btn--large\n      width: $button-large-height\n\n  &--floating\n    border-radius: 50%\n    min-width: 0\n    height: $button-floating-width\n    width: $button-floating-width\n    padding: 0\n\n    &:after\n      border-radius: 50%\n\n    .icon\n      height: $button-floating-icon-width\n      width: $button-floating-icon-width\n\n    &.btn--small\n      height: $button-floating-small-width\n      width: $button-floating-small-width\n\n      .icon\n        font-size: $button-floating-small-icon-width\n        height: $button-floating-small-icon-width\n        width: $button-floating-small-icon-width\n\n\n    &.btn--large\n      height: $button-floating-large-width\n      width: $button-floating-large-width\n\n      .icon\n        font-size: $button-floating-large-icon-width\n        height: $button-floating-large-icon-width\n        width: $button-floating-large-icon-width\n\n\n/** Themes */\n.btn\n  &--light\n    color: $color-light\n\n    &:hover:after\n      background-color: $button-light-overlay\n\n    // Do not show disabled styles when using loader\n    &.btn--disabled:not(.btn--loader)\n      color: $button-light-disabled-color !important\n      opacity: 1\n\n      &:not(.btn--flat):not(.btn--icon)\n        background-color: $button-light-overlay !important\n\n  &--dark\n    color: $color-dark\n\n    &:hover:after\n      background-color: $button-dark-overlay\n\n    // Do not show disabled styles when using loader\n    &.btn--disabled:not(.btn--loader)\n      color: $button-dark-disabled-color !important\n      opacity: 1\n\n      &:not(.btn--flat):not(.btn--icon)\n        background-color: $button-dark-overlay !important\n\n/** Sizes */\n.btn\n  &--small\n    font-size: $button-small-font-size\n    height: $button-small-height\n\n  &--large\n    font-size: $button-large-font-size\n    height: $button-large-height\n\n\n/** Loader */\n.btn\n  &--loader\n    pointer-events: none\n\n    .btn__content\n      opacity: 0\n\n  &__loading\n    position: absolute\n    width: 100%\n    height: 100%\n    left: 0\n    top: 0\n    display: flex\n    justify-content: center\n    align-items: center\n\n    .icon\n      &--left\n        margin-right: 1rem\n        line-height: inherit\n\n      &--right\n        margin-left: 1rem\n        line-height: inherit\n\n\n/** Custom Buttons */\n.btn\n  &--outline\n    border: 1px solid currentColor\n    background: transparent !important\n    box-shadow: none\n\n    &:hover\n      box-shadow: none\n\n  &--block\n    display: flex\n    flex: 1\n    margin: $button-margin 0\n    width: 100%\n\n  &--round\n    border-radius: 28px\n\n    &:after\n      border-radius: 28px\n\n\n/** Button w/ directional Icon */\n.btn\n  .icon\n    &--right\n      margin-left: 16px\n\n    &--left\n      margin-right: 16px\n",".application--dark\n  .card\n    background: $grey.darken-3\n\n.application--light\n  .card\n    background: $body-bg-light\n\n.card\n  elevation(2)\n  position: relative\n  border-radius: $card-border-radius\n  min-width: 0\n\n  &--raised\n    elevation(3, true)\n  \n  &--flat\n    box-shadow: none !important\n\n  &--hover\n    cursor: pointer\n    transition: all .4s cubic-bezier(.25, .8, .25, 1)\n    transition-property: box-shadow\n\n    &:hover\n      elevation(8, true)\n\n  &--horizontal\n    display: flex\n    flex-flow: row nowrap\n\n    > .card__row\n      flex: 0 1 30%\n\n    > .card__row, > .card__column\n      overflow: hidden\n\n      &:first-child\n        border-radius: $card-border-radius 0 0 $card-border-radius\n\n      &:last-child\n        border-radius: 0 $card-border-radius $card-border-radius 0\n        \n        > *:first-child\n          border-top-right-radius: $card-border-radius\n          \n        > *:last-child\n          border-bottom-right-raidius: $card-border-radius\n\n  &__column, &__row\n    display: flex\n\n  &__column\n    flex: 1\n    flex-flow: column nowrap\n\n  &__row\n    align-items: center\n    margin-top: auto\n    flex: 1 1 auto\n    flex-flow: row nowrap\n    min-height: $card-row-min-height\n\n    .card__text\n      height: 100%\n\n    &--actions\n      padding: 8px\n      height: 52px\n      flex: 0 0 auto\n      justify-content: flex-end\n      width: 100%\n\n      .btn\n        margin: 0\n        padding: 0 8px\n\n      &-stacked\n        flex-direction: column\n        margin: 0 !important\n        padding: 0 0 8px !important\n\n        > .btn\n          width: 100%\n          height: 48px\n          margin: 0 !important\n          padding: 0 16px !important\n          display: flex\n          flex-direction: row-reverse\n\n          span\n            display: table\n\n  &__title\n    border-top-left-radius: inherit\n    border-top-right-radius: inherit\n    font-size: $card-title-text-size\n    font-weight: $card-title-text-font-weight\n    letter-spacing: $card-title-text-letter-spacing\n    display: flex\n    flex: 1\n    align-self: center\n    align-items: center\n    padding: $card-padding\n\n    .btn\n      margin: 0\n\n  &__text\n    padding: $card-padding\n    width: 100%\n",".carousel\n  height: 500px\n  width: 100%\n  background: #000000\n  position: relative\n  overflow: hidden\n  elevation(2)\n\n  &__item\n    display: flex\n    align-items: center\n    justify-content: center\n    flex: 1 0 100%\n    height: 100%\n    width: 100%\n    background-size: cover\n    background-position: center center\n    transition: .2s ease-out\n  \n  &__left\n    position: absolute\n    top: 50%\n    z-index: 1\n    left: 5px\n    transform: translateY(-50%)\n    \n    .btn\n      color: #FFFFFF\n      margin: 0 !important\n      height: auto\n      width: auto\n      \n      i\n        font-size: 48px\n        \n      &:hover\n        background: none\n      \n  &__right\n    @extend .carousel__left\n    left: auto\n    right: 5px\n  \n  &__controls\n    background: rgba(0, 0, 0, .5)\n    align-items: center\n    bottom: 0\n    display: flex\n    justify-content: center\n    left: 0\n    position: absolute\n    height: 50px\n    list-style-type: none\n    width: 100%\n    z-index: 1\n    \n    &__item\n      color: #FFFFFF\n      margin: 0 1rem !important\n      \n      i\n        opacity: .5\n        transition: $primary-transition\n      \n      &--active\n        i\n          opacity: 1\n          vertical-align: middle\n          font-size: 2rem !important\n      \n      &:hover\n        background: none\n        \n        i\n          opacity: .8\n",".chip\n  align-items: center\n  background: $chip-background\n  border: $chip-border\n  border-radius: $chip-border-radius\n  cursor: default\n  display: inline-flex\n  justify-content: space-between\n  font-size: $chip-font-size\n  padding: $chip-padding\n  margin: $chip-margin\n  height: $chip-height\n  transition: .3s $transition.ease-in-out\n  vertical-align: middle\n  white-space: nowrap\n  color: $chip-label-color\n\n  .avatar\n    border-radius: 50%\n    height: $chip-height\n    width: $chip-height\n    min-width: $chip-height\n    margin-left: -14px\n    margin-right: 8px\n    color: #fff\n\n    img\n      width: 100%\n      height: 100%\n\n  &:focus\n    elevation(2)\n    outline: none\n\n  &--label\n    border-radius: $chip-label-border-radius\n\n  &--outline\n    background: $chip-outline-background\n    color: $chip-outline-color\n\n  &--small\n    height: $chip-small-height\n\n    .avatar\n      height: $chip-small-height\n      width: $chip-small-height\n      min-width: $chip-small-height\n\n  &__close\n    color: inherit\n    display: flex\n    align-items: center\n    text-decoration: none\n    font-size: $chip-icon-font-size\n    margin-left: 4px\n    margin-right: -2px\n    transition: .3s $transition.ease-in-out\n\n    &:hover\n      opacity: .8\n\n  &--removable\n    padding: $chip-removeable-padding\n\n  &--select-multi\n    margin: $chip-margin $chip-margin $chip-margin 0\n\n  .icon\n    color: inherit\n    opacity: .54\n\n  .icon--right\n    margin-left: 4px\n\n  .icon--left\n    margin-right: 4px\n",".datatable\n  thead\n    th.column.sortable\n      cursor: pointer\n\n      i\n        color: rgba($material-theme.fg-color, $material-theme.disabledORhints-text-percent)\n        font-size: 16px\n        vertical-align: sub\n        display: inline-block\n        opacity: 0\n        transition: .3s $transition.swing\n\n      &:hover\n        color: rgba($material-theme.fg-color, $material-theme.primary-text-percent)\n\n        i\n          opacity: .6\n\n      &.active\n        transform: none\n        color: rgba($material-theme.fg-color, $material-theme.primary-text-percent)\n\n        i\n          color: rgba($material-theme.fg-color, $material-theme.primary-text-percent)\n          opacity: 1\n\n        &.desc\n          i\n            transform: rotate(-180deg)\n\n.datatable\n  tfoot\n    .input-group__details\n      display: none\n\n/** Actions */\n.datatable__actions\n  color: rgba($material-theme.fg-color, $material-theme.secondary-text-percent)\n  display: flex\n  justify-content: flex-end\n  align-items: center\n  font-size: 12px\n\n  .btn\n    color: inherit\n\n    &:last-of-type\n      margin-left: 18px\n\n  &__pagination\n    text-align: center\n    margin: 0 26px 0 32px\n\n  &__select\n    display: flex\n    align-items: center\n    justify-content: center\n\n    .input-group--select\n      margin: 13px 0 13px 34px\n      position: initial\n\n      .input-group__selections__comma\n        color: rgba($material-theme.fg-color, $material-theme.secondary-text-percent) !important\n        font-size: 12px\n        padding-top: 6px\n\n      .input-group__append-icon\n        color: rgba($material-theme.fg-color, $material-theme.secondary-text-percent) !important\n\n.datatable__progress\n  tr\n    height: auto !important\n\n  th\n    padding: 0 !important\n\n    .progress-linear\n      top: -3px\n      margin: 0 0 -3px\n",".picker--date\n  color: #fff\n  width: 100%\n\n/** Years */\n.picker--date__years\n  background: #fff\n  color: #000\n  font-size: 18px\n  font-weight: 400\n  list-style-type: none\n  max-height: 300px\n  overflow: auto\n  padding: 0\n  text-align: center\n  \n  li\n    cursor: pointer\n    margin: 16px 0\n    transition: .3s $transition.swing\n    \n    &:hover\n      color: $theme.primary\n\n    &.active\n      color: $theme.primary\n      font-size: 24px\n      font-weight: 500\n      margin: 20px 0\n  \n\n/** Title */\n.picker--date__title\n  justify-content: space-between\n  flex-direction: column\n  flex-wrap: wrap\n  \n  &-year\n    font-size: 14px\n    \n  &-date\n    font-size: 34px\n    \n    > div\n      position: relative\n  \n  &-year, &-date\n    font-weight: 500\n    transition: .3s $transition.swing\n    width: 100%\n    \n    &:not(.active)\n      cursor: pointer\n      opacity: .6\n      \n    &:hover\n      opacity: 1\n    \n\n/** Header */\n.picker--date__header\n  color: #000\n  padding: 4px 16px\n\n  &-selector\n    align-items: center\n    display: flex\n    justify-content: space-between\n    position: relative\n    \n    .btn\n      color: #000\n      margin: 0\n    \n    .icon\n      cursor: pointer\n      user-select: none\n      \n    &-date\n      flex: 1\n      text-align: center\n      position: relative\n      overflow: hidden\n      \n      strong\n        transition: .3s $transition.swing\n        display: block\n        width: 100%\n\n\n.picker--date\n  &__table\n    position: relative\n\n  table\n    transition: .3s $transition.swing\n    top: 0\n\n    thead\n      th\n        padding: 8px 0\n\n    th\n      color: rgba(#000, .54)\n      font-weight: 600\n      font-size: 12px\n\n    th, td\n      text-align: center\n      width: 45px\n      \n    .btn\n      margin: 0\n      \n      &__content\n        overflow: visible\n        transition: none\n        z-index: 1\n      \n    .btn.btn--floating.btn--current:not(.btn--active)\n      color: rgba($theme.primary, .6)\n      \n    .btn.btn--floating\n      height: 32px\n      width: 32px\n\n      &:after\n        background: $theme.primary !important\n        opacity: 0\n        transform: scale(0)\n        \n      &:not(.btn--active):hover\n        color: #fff\n\n        &:after\n          opacity: .6\n          transform: scale(1)\n\n    .btn.btn--floating.btn--active\n      color: #fff\n      \n      &:after\n        background: $theme.primary !important\n        opacity: 1\n        transform: none\n        \n.picker--date\n  &.picker--dark\n    .picker--date__header, .picker--date__years\n      background: $grey.darken-3\n      color: #fff\n      \n      .btn\n        color: #fff\n        \n    .picker--date__table\n      table\n        th, td, .btn\n          color: #fff\n          \n        .btn.btn--floating\n          &:after\n            background: $theme.accent !important\n\n        .btn--active\n          color: #000\n\n        .btn--floating.btn--current:not(.btn--active)\n          color: $theme.accent\n          \n.picker--landscape\n  .picker--date__years\n    margin-left: 170px\n    width: 330px",".dialog\n  elevation(24)\n  border-radius: $card-border-radius\n  margin: 24px 40px\n  pointer-events: auto\n  transition: .3s ease-in-out\n  \n  &__content\n    align-items: center\n    display: flex\n    height: 100%\n    justify-content: center\n    left: 0\n    pointer-events: none\n    position: fixed\n    top: 0\n    transition: .3s ease-in-out\n    width: 100%\n    z-index 5\n  \n  &:not(.dialog--fullscreen)\n    max-width: 90%\n    max-height: 90%\n  \n  &:not(.dialog--fullscreen)\n    max-width: 90%\n    max-height: 90%\n  \n  &__container\n    display: inline-block\n    vertical-align: middle\n\n  &--fullscreen\n    margin 0\n    width 100%\n    height 100%\n    position fixed\n    overflow-y: auto\n    top: 0\n    left 0\n    padding-top: 56px\n\n    .toolbar\n      height 56px           // TODO: height with two lines of text: 80dp\n      padding 0 !important\n      position: fixed\n      top: 0\n\n      &__title\n        font-size 20px      // TODO: convert from 20sp\n        padding: 20px 0     // TODO: convert from 20dp\n\n      .btn\n        &:first-child      // TODO: full width 72dp\n          max-width: 24px\n          max-height: 24px\n          margin: 0 32px 0 16px !important\n        &:last-child\n          margin 0 !important\n          height 100%\n          font-size 14px    // TODO: convert from 14sp\n\n    >\n      .card\n        min-height 100%\n        min-width 100%\n        margin 0 !important\n        padding 0 !important\n      \n  &--scrollable\n    .card__row:not(.card__row--actions)\n      overflow-y: auto",".application--light\n  .divider\n    background: $material-twelve-percent-dark\n\n.application--dark\n  .divider\n    background: $material-twelve-percent-light\n\n.divider\n  border: none\n  display: block\n  height: 1px\n  flex: 1\n  width: 100%\n  \n  &--inset\n    margin-left: 72px\n    width: calc(100% - 72px)\n    \n  &.divider--dark\n    background: $material-twelve-percent-light\n\n  &.divider--light\n    background: $material-twelve-percent-dark",".application--dark\n  .expansion-panel\n    color: $color-light\n\n.application--light\n  .expansion-panel\n    color: $color-dark\n\n.expansion-panel\n  text-align: left\n  list-style-type: none\n  padding: 0\n  width: 100%\n  elevation(2)\n\n  > li\n    border: 1px solid rgba($material-theme.fg-color, $material-theme.divider-percent)\n    &:not(:first-child)\n      border-top: none\n\n  &__header\n    display: flex\n    cursor: pointer\n    align-items: center\n    height: $expansion-panel-collapse-height\n    position: relative\n    padding-left: 1rem\n\n    i\n      margin-right: 1rem\n\n    &:after\n      content: '\\E313'\n      font-family: 'Material Icons'\n      font-size: 1.5rem\n      position: absolute\n      right: 15px\n      top: calc(50% - 16px)\n      color: inherit\n      transition: transform 0.3s cubic-bezier(0, 0, .2, 1)\n\n    &--active\n      &:after\n        transform: rotate(-180deg)\n\n  &__body\n    background-color: rgba($material-theme.fg-color, 0.03)\n    transition: $primary-transition\n\n    .card\n      border-radius: 0\n      elevation(0)\n",".fab\n  @extend .btn, .btn--floating, .btn--raised\n\n  position: fixed\n  bottom: 125px\n  right: 24px\n  \n  &--hidden\n    transform: scale(0) rotate(-45deg)\n    \n  &--lateral\n    .fab__content\n      transform: none !important\n  \n  @keyframes is-changing\n    0%\n      transform: scale(1)\n    \n    50%\n      transform: scale(0)\n      \n    100%\n      transform: scale(1)\n  \n  @keyframes lateral\n    0%\n      opacity: 0\n      \n    60%\n      opacity: 0\n\n    100%\n      opacity: 1\n\n  &--is-changing\n    animation-duration: .6s\n    animation-name: is-changing\n    animation-delay: 50ms\n    \n    .fab__content\n      animation-name: lateral\n      animation-duration: .6s\n      \n  &:focus\n    .fab__content\n      transform: rotate(45deg)\n\n  &.fab--small\n    height: $button-floating-small-width\n    width: $button-floating-small-width\n\n    .icon\n      font-size: $button-floating-small-icon-width\n      height: $button-floating-small-icon-width\n      width: $button-floating-small-icon-width\n\n\n  &.fab--large\n    height: $button-floating-large-width\n    width: $button-floating-large-width\n\n    .icon\n      font-size: $button-floating-large-icon-width\n      height: $button-floating-large-icon-width\n      width: $button-floating-large-icon-width",".footer\n  align-items: center\n  background: $theme.primary\n  color: #fff\n  display: flex\n  height: $footer-height\n  transition: .3s $transition.swing\n  \n  &--absolute, &--fixed\n    bottom: 0\n    left: 0\n    width: 100%\n    z-index: 3\n\n  &--absolute\n    position: absolute\n    \n  &--fixed\n    position: fixed\n  \n  > *:first-child\n    margin-left: $grid-gutter\n    \n  > *:last-child\n    margin-right: $grid-gutter\n    \n  @media $display-breakpoints.xs-only\n    > *:first-child\n      margin-left: $grid-gutter-mobile\n      \n    > *:last-child\n      margin-right: $grid-gutter-mobile\n  ",".icon\n  align-items: center\n  display: inline-flex\n  font-size: 24px\n  justify-content: center\n  vertical-align: middle\n\n  &--dark\n    icon(rgba($material-light.fg-color, $material-light.active-icon-percent), rgba($material-light.fg-color, $material-light.inactive-icon-percent))\n\n  &--light\n    icon(rgba($material-dark.fg-color, $material-dark.active-icon-percent), rgba($material-dark.fg-color, $material-dark.inactive-icon-percent))\n\n  &--large\n    font-size: 2.5rem\n\n  &--medium\n    font-size: 2rem\n\n  &--x-large\n    font-size: 3rem\n","icon(active, inactive)\n  color: active\n  \n  &.icon--disabled\n    color: inactive",".input-group\n  display: flex\n  flex: 1\n  flex-wrap: wrap\n  min-width: 24px\n  margin: $input-group-margin\n  position: relative\n  width: 100%\n  outline: none\n\n\n/** Label */\n.input-group\n  label\n    font-size $input-font-size\n    line-height: $input-font-size * 2\n    height: $input-height\n    max-width: 80%\n    transition: .4s $transition.fast-in-fast-out\n    z-index: 0\n\n/** Blocks */\n.input-group\n  &__input\n    display: flex\n    flex: 1 0 100%\n    min-width: 0\n    min-height: $input-height\n\n\n/** States */\n.input-group\n  &--disabled\n    .input-group__details:before\n      background-color: transparent\n      background-position: bottom\n      background-size: 3px 1px\n      background-repeat: repeat-x\n\n    &.input-group--light\n      .input-group__details:before\n        background-image: linear-gradient(to right, rgba(255, 255, 255, .38) 0, rgba(255, 255, 255, .38) 33%, transparent 0)\n\n    &.input-group--dark\n      .input-group__details:before\n        background-image: linear-gradient(to right, rgba(0, 0, 0, .38) 0, rgba(0, 0, 0, .38) 33%, transparent 0)\n\n  &--focused, &:focus\n    &:not(.input-group--disabled)\n      .input-group__details\n        &:after\n          width: 100%\n\n  &--active\n    //\n\n  &--error\n    .input-group__details:after\n      background-color: $theme.error\n\n  &--light\n    .input-group__hint\n      color: rgba($material-dark.fg-color, .54)\n\n    .input-group__details:before\n      background-color: $material-twelve-percent-light\n\n    .icon\n      color: rgba($material-dark.fg-color, .6)\n\n  &--dark\n    .input-group__hint\n      color: rgba($material-light.fg-color, .7)\n\n    .input-group__details:before\n      background-color: $material-twelve-percent-dark\n\n    .icon\n      color: rgba($material-light.fg-color, .6)\n\n/** Icons */\n.input-group\n  &__icon-cb\n    cursor: pointer\n\n/** Details */\n.input-group\n  .slide-y-transition-leave,\n  .slide-y-transition-leave-to\n    position: absolute\n\n  &__details\n    display: flex\n    padding-top: 4px\n    flex: 1 0 100%\n    font-size: 12px\n    min-height: 22px\n    position: relative\n    width: 100%\n\n    &:after, &:before\n      content: ''\n      position: absolute\n      left: 0\n\n    &:after\n      background-color: $theme.primary\n      top: -1px\n      height: 2px\n      transition: .3s $transition.fast-out-slow-in\n      width: 0%\n      z-index: 1\n\n    &:before\n      top: 0\n      height: 1px\n      width: 100%\n      z-index: 0\n\n  &--hide-details\n    .input-group__details\n      min-height: 0\n      padding: 0\n\n/** Hint */\n.input-group\n  &__hint\n    // flex: 1 0\n    transition: .3s $transition.fast-in-fast-out\n\n/** Error */\n.input-group\n  .input-group__error\n    flex: 1 0\n    transition: .3s $transition.fast-in-fast-out\n    color: $theme.error\n",".list\n  lists()\n  padding-top: $list-top-padding\n  padding-bottom: $list-bottom-padding\n  transition: height .4s $transition.fast-out-slow-in\n\n  .input-group\n    margin: 0\n\n  > .list__item ~ .list__item\n    margin-top: 0\n\n  &__item\n    position: relative\n\n  &__tile\n    color: $color-theme\n    display: flex\n    height: $list-item-single-height\n    text-decoration: none\n    align-items: center\n    padding: 0 16px\n    margin: 0\n    transition: $primary-transition\n    position: relative\n\n    &:after\n      content: ''\n      position: absolute\n      left: 0\n      top: 0\n      height: 1px\n      opacity: 0\n      width: 100%\n      transition: $primary-transition\n      background-color: $material-twelve-percent-dark\n\n    a&:hover, &--highlighted\n      background: $material-twelve-percent-dark\n\n    &__content,\n    &__action,\n    &__avatar\n      height: 100%\n\n    &__title, &__sub-title\n      white-space: nowrap\n      overflow-x: hidden\n      text-overflow: ellipsis\n      width: 100%\n\n    &__title\n      font-size: $list-title-font-size\n      // line-height: 1.15\n      font-weight: $list-title-font-weight\n      transition: $primary-transition\n      position: relative\n      text-align: left\n\n    &__sub-title\n      color: rgba($material-theme.fg-color, $material-theme.secondary-text-percent)\n      font-size: $list-sub-title-font-size\n      // line-height: 1.3\n      font-weight: $list-sub-title-font-weight\n\n    .avatar\n      justify-content: flex-start\n      min-width: 56px\n\n    &__action\n      display: flex\n      justify-content: flex-start\n      min-width: 56px\n      align-items: center\n\n      .input-group\n        align-items: center\n\n      .input-group__details\n        display: none\n\n      .icon\n        transition: $primary-transition\n\n      &-text\n        color: $grey.base\n        font-size: $list-action-text-font-size\n\n      &--stack\n        align-items: flex-end\n        justify-content: space-between\n        padding-top: $list-top-padding\n        padding-bottom: $list-bottom-padding\n        white-space: nowrap\n        flex-direction: column\n\n    &__content\n      text-align: left\n      flex: 0 1 100%\n      font-size: $list-content-font-size\n      overflow: hidden\n      display: flex\n      align-items: flex-start\n      justify-content: center\n      flex-direction: column\n\n      + .avatar\n        justify-content: flex-end\n\n      + .list__tile__action:not(.list__tile__action--stack)\n        justify-content: flex-end\n\n    &--active\n      .list__tile__title\n        color: $theme.primary\n\n    &--disabled\n      opacity: 0.4\n      pointer-events: none\n\n    &--avatar\n      height: $list-item-avatar-single-height\n\n    &--select-multi\n      padding: 0 10px\n\n  &--dense\n    padding-top: $list-item-dense-top-padding\n\n    .list__tile\n      height: $list-item-dense-single-height\n      font-size: $list-item-dense-font-size\n\n    .list__tile__title\n      font-size: $list-item-dense-font-size\n\n    .list__tile__sub-title\n      font-size: $list-item-dense-font-size\n\n  &--two-line\n    .list__tile\n      height: $list-item-double-height\n\n    &.list--dense\n      .list__tile\n        height: $list-item-dense-double-height\n\n  &--three-line\n    .list__tile\n      height: $list-item-triple-height\n\n    .list__tile__sub-title\n      white-space: initial\n      -webkit-line-clamp: 2\n      -webkit-box-orient: vertical\n      display: -webkit-box\n\n    &.list--dense\n      .list__tile\n        height: $list-item-dense-triple-height\n\n  &--group\n    position: relative\n    padding: 0\n\n    &:after\n      content: ''\n      position: absolute\n      left: 0\n      bottom: 0\n      height: 1px\n      opacity: 0\n      width: 100%\n      background-color: $material-twelve-percent-dark\n\n    .list__tile\n      padding-left: $list-item-left-padding\n\n      .list__tile__title\n        font-weight: $list-group-tile-title-font-weight\n\n    .list__tile--active\n      .list__tile__title\n        color: $theme.primary\n        font-weight: $list-group-tile-title-active-font-weight\n\n    &__header\n      + .list--group\n        &:after\n          opacity: 1\n\n      &--active\n        .list__tile\n          background: $material-twelve-percent-dark\n\n          &:after\n            opacity: 1\n\n          .list__tile__title\n            color: inherit\n\n          .list__tile__action:last-of-type\n            .icon\n              transform: rotate(-180deg)\n\n      &--no-action + .list--group\n        .list__tile\n          padding-left: $list-left-padding\n\n  &--subheader\n    padding-top: 0\n",".menu\n  display: inline-block\n  position: relative\n  vertical-align: middle\n\n  &--disabled\n    cursor: not-allowed\n\n    .menu__activator\n      cursor: not-allowed\n\n  &__activator\n    align-items: center\n    cursor: pointer\n    position: relative\n\n  &__content\n    background: $material-theme.bg-color\n    position: absolute\n    display: inline-block\n    border-radius: 2px\n    overflow-y: auto\n    overflow-x: hidden\n    z-index: 6\n    elevation(8)\n\n  &-transition\n    &-enter\n      .list__tile\n        min-width: 0\n        transition-delay: .4s\n        opacity: 0\n        transform: translateY(-15px)\n        pointer-events: none\n\n      .list__tile--active\n        opacity: 1\n        transform: none !important\n        pointer-events: auto\n    &-enter-to\n      .list__tile\n        pointer-events: auto\n        opacity: 1\n        \n      .list__tile--active\n        transform: none !important\n      \n    &-leave-to\n      transform: translateY(-10px)\n    \n    &-leave-active, &-leave-to\n      pointer-events: none\n        \n    &-enter, &-leave-to\n      opacity: 0\n\n    &-enter-to, &-leave\n      opacity: 1\n\n    &-enter-active, &-leave-active\n      transition: all .5s $transition.swing\n",".navigation-drawer\n  max-width: 100%\n  overflow-y: auto\n  overflow-x: hidden\n  padding: 0 0 100px\n  pointer-events: auto\n  position: fixed\n  transition: .3s $transition.swing\n  width: $navigation-drawer-width\n  top: 0\n  left: 0\n  will-change: transform\n  z-index: 3\n  \n  // Remove animation when booting up\n  &:not(.navigation-drawer--is-booted)\n    transition: none !important\n\n    + .toolbar, ~ main, ~ .footer\n      transition: none !important\n\n  &--close:not(.navigation--permanent)\n    &.navigation-drawer:not(.navigation-drawer--right)\n      transform: \"translate3d(-%s, 0, 0)\" % ($navigation-drawer-width)\n\n    &.navigation-drawer--right\n      transform: \"translate3d(%s, 0, 0)\" % ($navigation-drawer-width)\n\n  &--right\n    left: initial\n    right: 0\n\n  &--absolute\n    position: absolute\n\n  &--dark\n    background: $theme.secondary\n    navigation-drawer($material-dark.fg-color)\n\n    .divider\n      background: $material-twelve-percent-light\n\n  &--light\n    background: $material-light.bg-color\n    navigation-drawer(rgba($material-light.fg-color, .87))\n\n    .divider\n      background: $material-twelve-percent-dark\n      \n    &:not(.navigation-drawer--right)\n      border-right: 1px solid rgba($material-light.fg-color, $material-light.divider-percent)\n    \n    &.navigation-drawer--right\n      border-left: 1px solid rgba($material-light.fg-color, $material-light.divider-percent)\n\n  &--permanent, &--persistent\n    &.navigation-drawer--clipped, &.navigation-drawer--floating\n      margin-top: $toolbar-height\n      max-height: \"calc(100vh - %s)\" % $toolbar-height\n\n      ~ .toolbar, ~ .footer.footer--fixed, ~ .footer.footer--absolute\n        padding-left: 0\n        z-index: 3\n\n    &.navigation-drawer--floating\n      border-color: transparent\n\n  &--permanent\n    &:not(.navigation-drawer--clipped):not(.navigation-drawer--floating)\n      ~ main,\n      ~ .toolbar,\n      ~ .footer\n        padding-left: $navigation-drawer-width\n\n  &--persistent\n    &.navigation-drawer--open:not(.navigation-drawer--is-mobile):not(.navigation-drawer--right)\n      &:not(.navigation-drawer--clipped)\n        ~ .toolbar\n          padding-left: $navigation-drawer-width\n\n      ~ main, ~ .footer:not(.footer--fixed):not(.footer--absolute)\n        padding-left: $navigation-drawer-width\n\n    &.navigation-drawer--open.navigation-drawer--right:not(.navigation-drawer--is-mobile)\n      &:not(.navigation-drawer--clipped)\n        + .toolbar\n          padding-right: $navigation-drawer-width\n\n      ~ main, ~ .footer:not(.footer--fixed):not(.footer--absolute)\n        padding-right: $navigation-drawer-width\n\n  &--mini-variant\n    margin-top: $toolbar-height\n    max-height: \"calc(100vh - %s)\" % $toolbar-height\n    overflow: hidden\n    width: $navigation-drawer-mini-variant-width\n\n    .list__tile__action, .list__tile__avatar\n      justify-content: center\n      min-width: 48px\n\n    .list__tile__content\n      opacity: 0\n\n    .subheader, .divider\n      display: none\n\n    ~ .toolbar\n      padding-left: 0 !important\n\n    &:not(.navigation-drawer--close)\n      ~ main\n        padding-left: $navigation-drawer-mini-variant-width !important\n\n      ~ .footer:not(.footer--fixed):not(.footer--absolute)\n        padding-left: $navigation-drawer-mini-variant-width !important\n\n  &--temporary, &--is-mobile:not(.navigation-drawer--permanent)\n    z-index: 5\n\n    &:not(.navigation-drawer--close)\n      elevation(16)\n\n  ~ toolbar\n    + main\n      min-height: \"calc(100vh - %s)\" % $toolbar-height\n\n  > .list\n    .list__tile\n      transition: none\n\n      &--active\n        .list__tile__title\n          color: inherit\n\n        > *:first-child\n          .icon\n            color: $theme.primary\n\n    .list--group\n      &__header--active\n        &:after\n          background: transparent\n\n        .list__tile__action:first-of-type\n          .icon\n            color: $theme.primary\n\n    .list--group__container\n      .list__tile--active\n        .list__tile__title\n          color: $theme.primary\n","navigation-drawer(color)\n  > .list\n    .list__tile\n      color: color\n      \n    .list--group__header--active\n      .list__tile\n        &:after\n          background: rgba(color, .12)\n        \n      + .list--group\n        &:after\n          background: rgba(color, .12)\n",".overlay\n  position: fixed\n  top: 0\n  left: 0\n  right: 0\n  bottom: 0\n  z-index: 4\n\n  &--absolute\n    position: absolute\n\n  &:before\n    background-color: rgba(33, 33, 33, 1)\n    bottom: 0\n    content: ''\n    filter: blur(10%)\n    height: 100%\n    left: 0\n    opacity: 0\n    position: absolute\n    right: 0\n    top: 0\n    transition: $primary-transition\n    width: 100%\n\n  &--active\n    pointer-events: auto\n\n    &:before\n      opacity: .46\n",".pagination\n  list-style-type: none\n  display: inline-flex\n  align-items: center\n  padding: 0\n  margin: 0\n  height: 40px\n  align-items: center\n\n  > li\n    align-items: center\n    display: flex\n\n  a\n    transition: .3s $transition.linear-out-slow-in\n\n    &:hover\n      elevation(2)\n\n  &--circle\n    .pagination__item,\n    .pagination__more,\n    .pagination__navigation\n      border-radius: 50%\n\n  &--disabled\n    pointer-events: none\n    opacity: .6\n\n  &__item\n    elevation(2)\n    border-radius: 4px\n    color: $color-theme\n    display: inline-flex\n    justify-content: center\n    align-items: center\n    background: transparent\n    height: 34px\n    width: 34px\n    margin: .3rem\n    text-decoration: none\n\n    &--active\n      elevation(4)\n      background: $theme.primary\n      color: $color-light\n\n  &__navigation\n    elevation(2)\n    display: inline-flex\n    justify-content: center\n    align-items: center\n    text-decoration: none\n    color: $color-theme\n    height: 2rem\n\n    border-radius: 4px\n    width: 2rem\n    margin: .3rem 15px\n\n    .icon\n      font-size: 2rem\n      transition: $secondary-transition\n      vertical-align: middle\n      color: rgba($material-theme.fg-color, $material-theme.active-icon-percent)\n\n    &--disabled\n      opacity: .6\n      pointer-events: none\n\n  &__more\n    margin: .3rem\n    display: inline-flex\n    align-items: flex-end\n    justify-content: center\n    height: 2rem\n    width: 2rem\n",".parallax\n  position: relative\n  overflow: hidden\n  z-index: 0\n  \n  &__image-container\n    position: absolute\n    top: 0\n    left: 0\n    right: 0\n    bottom: 0\n    z-index: 1\n    \n  &__image\n    position: absolute\n    bottom: 0\n    left: 50%\n    min-width: 100%\n    min-height: 100%\n    display: none\n    transform: translate3d(-50%, 0, 0)\n    z-index: 1\n      \n  &__content\n    color: #FFFFFF\n    height: 100%\n    z-index: 2\n    position: relative\n    display: flex\n    flex-direction: column\n    justify-content: center\n    padding: 0 1rem",".picker\n  border-radius: $card-border-radius\n  // color: $material-theme.fg-color\n  display: flex\n  flex-direction: column\n  width: 290px\n\n  .card__row--actions\n    border: none\n    margin-top: -20px\n\n  &__title\n    height: 105px\n    border-top-left-radius: $card-border-radius\n    border-top-right-radius: $card-border-radius\n    padding: 16px\n\n  &__body\n    // color: #000\n    height: 290px\n    overflow: hidden\n    position: relative\n\n  &--dark\n    color: $color-light\n\n    .btn\n      color: $color-light\n\n    .picker__body\n      background: $grey.darken-3\n\n    .picker__title\n      background: $grey.darken-2\n\n  &--light\n    color: $color-light\n\n    .btn\n      color: $color-dark\n\n    .picker__body\n      background: $material-light.bg-color\n      color: $color-dark\n\n    .picker__title\n      background: $theme.primary\n\n  &--landscape\n    flex-direction: row\n    flex-wrap: wrap\n    width: 500px\n\n    .picker__title\n      border-top-right-radius: 0\n      border-bottom-right-radius: 0\n      flex: 0 1 170px\n      width: 170px\n      height: auto\n      position: absolute\n      top: 0\n      left: 0\n      height: 100%\n      z-index: 1\n\n    .picker__body\n      flex: 1 0\n      width: 330px\n      margin-left: 170px\n\n    .card__row--actions\n      margin-left: 170px\n      width: 330px\n",".progress-circular\n  position: relative\n  display: inline-flex\n  \n  &--indeterminate\n    svg\n      animation: $progress-circular-rotate-animation\n      transform-origin: center center\n      width: 100%\n      height: 100%\n      margin: auto\n      position: absolute\n      top: 0\n      bottom: 0\n      left: 0\n      right: 0\n      transition: $process-circular-intermediate-svg-transition\n      z-index: 0\n      \n    .progress-circular__overlay\n      animation: $progress-circular-rotate-dash\n      stroke-linecap: round\n      stroke-dasharray: 1,200\n      stroke-dashoffset: 0px\n    \n  &__underlay\n    stroke: $progress-circular-underlay-stroke\n    z-index: 1\n    \n  &__overlay\n    stroke: currentColor\n    z-index: 2\n    transition: $progress-circular-overlay-transition\n    \n  &__info\n    position: absolute\n    top: 50%\n    left: 50%\n    transform: translate3d(-50%, -50%, 0)\n    \n  @keyframes progress-circular-dash\n    0%\n      stroke-dasharray: 1,200\n      stroke-dashoffset: 0px\n    \n    50%\n      stroke-dasharray: 100,200\n      stroke-dashoffset: -15px\n    \n    100%\n      stroke-dasharray: 100,200\n      stroke-dashoffset: -125px\n      \n  @keyframes progress-circular-rotate\n    100%\n      transform: rotate(360deg)",".progress-linear\n  background: transparent\n  margin: 1rem 0\n  overflow: hidden\n  width: 100%\n  position: relative\n  progress-linear($theme.primary)\n  \n  &__bar\n    width: 100%\n    height: inherit\n    position: relative\n    transition: .3s ease-in\n    z-index: 1\n    \n    &__determinate\n      height: inherit\n      transition: .3s ease-in\n      \n    &__indeterminate\n      &:before, &:after\n        content: ''\n        height: inherit\n        position: absolute\n        left: 0\n        top: 0\n        bottom: 0\n        will-change: left, right\n        width: auto\n      \n      &:before\n        animation: indeterminate\n        animation-duration: 2.2s\n        animation-iteration-count: infinite\n        \n      &:after\n        animation: indeterminate-short\n        animation-duration: 2.2s\n        animation-iteration-count: infinite\n        \n  &--query\n    .progress-linear__bar__indeterminate\n      &:before\n        animation: query\n        animation-duration: 2s\n        animation-iteration-count: infinite\n        \n      &:after\n        animation: query-short\n        animation-duration: 2s\n        animation-iteration-count: infinite  \n\n  &--secondary\n    progress-linear($theme.secondary)\n  \n  &--success\n    progress-linear($theme.success)\n  \n  &--info\n    progress-linear($theme.info)\n    \n  &--warning\n    progress-linear($theme.warning)\n    \n  &--error\n    progress-linear($theme.error)\n        \n  @keyframes indeterminate\n    0%\n      left: -90%\n      right: 100%\n    60%\n      left: -90%\n      right: 100%\n    100%\n      left: 100%\n      right: -35%\n      \n  @keyframes indeterminate-short\n    0%\n      left: -200%\n      right: 100%\n    60%\n      left: 107%\n      right: -8%\n    100%\n      left: 107%\n      right: -8%\n      \n  @keyframes query\n    0%\n      right: -90%\n      left: 100%\n    60%\n      right: -90%\n      left: 100%\n    100%\n      right: 100%\n      left: -35%\n      \n  @keyframes query-short\n    0%\n      right: -200%\n      left: 100%\n    60%\n      right: 107%\n      left: -8%\n    100%\n      right: 107%\n      left: -8%","progress-linear(progress)\n  .progress-linear__bar\n    background: lighten(progress, 50%)\n    \n  .progress-linear__bar__determinate\n    background: progress\n    \n  .progress-linear__bar__indeterminate\n    &:before, &:after\n      background: progress",".ripple    \n  &__container\n    color: inherit\n    border-radius: inherit\n    position: absolute\n    width: 100%\n    height: 100%\n    left: 0\n    top: 0\n    overflow: hidden\n    z-index: 0\n    pointer-events: none\n          \n  &__animation\n    color: inherit\n    position: absolute\n    top: 0\n    left: 0\n    border-radius: 50%\n    background: currentColor\n    opacity: 0\n    transition: $ripple-animation-transition\n    pointer-events: none\n    overflow: hidden\n    will-change: opacity\n      \n    &--enter\n      transition: none\n      \n    &--visible\n      opacity: $ripple-animation-visible-opacity",".input-group--select\n  .input-group--select__autocomplete\n    display: block\n    opacity: 0\n    height: 0\n\n  .input-group__append-icon\n    transition: .3s $transition.linear-out-slow-in\n\n  &.input-group--focused\n    .input-group--select__autocomplete\n      flex: 1 0 100%\n      opacity: 1\n      height: 30px\n\n    .input-group__append-icon\n      transform: rotate(-180deg)\n\n  .input-group__input, input\n    cursor: pointer\n\n  &.input-group--disabled\n    cursor: not-allowed\n    pointer-events: none\n\n.input-group--select\n  .input-group__selections\n    display: flex\n    flex-wrap: wrap\n    width: 100%\n    position: relative\n\n    > div\n      display: flex\n      flex-wrap: wrap\n      flex: 1\n\n    &__comma\n      display: inline-block\n      font-size: 16px\n      height: 30px\n      padding-top: 4px\n      padding-right: 4px\n\n.input-group--select\n  &.input-group--light\n    .input-group__selections__comma\n      color: $color-light\n\n    .input-group__selections__comma--active\n      color: $theme.primary\n\n  &.input-group--dark\n    .input-group__selections__comma\n      color: $color-dark\n\n.input-group--select\n  .menu\n    display: inline\n\n  .fade-transition-leave-active\n    position: absolute\n    left: 0\n","/** Input */\n.input-group.input-group--selection-controls\n  display: flex\n\n  .icon\n    cursor: pointer\n    position: absolute\n    left: 0\n    user-select: none\n    transition: .3s $transition.ease-in-out\n\n  .input-group__details:before, .input-group__details:after\n    display: none\n\n  .input-group__input\n    width: 100%\n    position: relative\n\n    // Needed for safari\n    .icon\n      align-self: center\n      height: 24px\n      margin: auto\n\n/** Label */\n.input-group--selection-controls label\n  cursor: pointer\n  margin-left: 32px\n  position: absolute\n  left: 0\n  z-index: 1\n\n.input-group--selection-controls__ripple\n  border-radius: 50%\n  height: 48px\n  width: 48px\n  cursor: pointer\n  position: absolute\n  transform: translate3d(-12px, -50%, 0)\n  transform-origin: center center\n  top: 50%\n  left: 0\n\n  &:before\n    content: ''\n    position: absolute\n    width: 36px\n    height: 36px\n    background: currentColor\n    border-radius: 50%\n    left: 50%\n    top: 50%\n    transform: translate3d(-50%, -50%, 0) scale(.3)\n    opacity: 0\n    transition: $ripple-animation-transition\n    transform-origin: center center\n\n\n/** Switch */\n.input-group.input-group--selection-controls\n  z-index: 0\n\n  &.switch\n    .input-group--selection-controls__container\n      position: relative\n\n      &.primary--text\n      &.secondary--text\n      &.success--text\n      &.info--text\n      &.warning--text\n      &.error--text\n        .input-group--selection-controls__ripple--active:after\n          background: currentColor !important\n\n    .input-group--selection-controls__toggle\n      color: inherit\n      position: absolute\n      height: 14px\n      top: 50%\n      left: 0\n      width: 34px\n      border-radius: 8px\n      transform: translateY(-50%)\n\n    .input-group--selection-controls__ripple\n      transform: translate3d(-15px, -24px, 0)\n      transition: .3s $transition.fast-in-fast-out\n      z-index: 1\n\n      &:after\n        content: ''\n        position: absolute\n        display: inline-block\n        cursor: pointer\n        width: 20px\n        border-radius: 50%\n        top: 50%\n        left: 50%\n        transform: translate3d(-50%, -50%, 0)\n        height: 20px\n        elevation(1)\n        transition: all 100ms linear\n\n      &--active\n        transform: translate3d(2px, -24px, 0)\n\n    label\n      margin-left: 44px\n\n/** Radio */\n.input-group--selection-controls\n  &.input-group--tab-focused\n    .input-group--selection-controls__ripple\n      &:before\n        transform: translate3d(-50%, -50%, 0) scale(1)\n        opacity: $ripple-animation-visible-opacity\n\n/* Theme */\n.input-group--selection-controls\n  &.switch\n    &.input-group\n      &--light\n        selection-controls-switch-theme($selection-switch-light-color,\n          $selection-switch-light-background,\n          $selection-switch-light-label-color,\n          $selection-switch-light-enabled-color,\n          $selection-switch-light-disabled-color,\n          $selection-switch-light-disabled-background)\n      &--dark\n        selection-controls-switch-theme($selection-switch-dark-color,\n          $selection-switch-dark-background,\n          $selection-switch-dark-label-color,\n          $selection-switch-dark-enabled-color,\n          $selection-switch-dark-disabled-color,\n          $selection-switch-dark-disabled-background)\n\n  &.checkbox, &.radio\n    &.input-group\n      &--light\n        selection-controls-checkbox-theme($selection-controls-light-color,\n          $selection-controls-light-label-color,\n          $selection-controls-light-disabled-color,\n          $selection-controls-active-color)\n\n      &--dark\n        selection-controls-checkbox-theme($selection-controls-dark-color,\n          $selection-controls-dark-label-color,\n          $selection-controls-dark-disabled-color,\n          $selection-controls-active-color)\n\n      &.input-group--active\n        &.primary--text\n        &.secondary--text\n        &.success--text\n        &.info--text\n        &.warning--text\n        &.error--text\n          .icon, .input-group--selection-controls__ripple\n            color: currentColor !important\n","selection-controls-switch-theme(color, background, labelColor, enabledColor, disabledColor, disabledBackground)\n  color: enabledColor\n\n  label\n    color: labelColor\n\n  .input-group--selection-controls__toggle\n    background: color\n\n    &--active\n      background: currentColor\n      opacity: .5\n\n  .input-group--selection-controls__ripple\n    color: currentColor\n\n    &:after\n      background: background\n\n    &--active\n      &:after\n        background: currentColor\n\n  .input-group--selection-controls__container--disabled\n    color: disabledColor\n\n    .input-group--selection-controls__toggle\n      background: disabledBackground\n      opacity: 1\n\n    .input-group--selection-controls__ripple\n      color: disabledColor\n\n      &:after\n        background: disabledColor\n\nselection-controls-checkbox-theme(color, labelColor, disabledColor, activeColor)\n  label\n    color: labelColor\n\n  .icon\n    color: color\n\n  &.input-group--active\n    .input-group--selection-controls__ripple\n      color: activeColor\n\n    .icon\n      color: activeColor\n\n  &.input-group--disabled\n    .input-group--selection-controls__ripple\n      color: disabledColor\n\n    .icon\n      color: disabledColor\n","slider-theme(off, focus, label, thumb, ticks)\n  label\n    transform: none\n    flex-basis: 56px\n    color: label\n    display: flex\n    font-size: 18px\n    align-items: center\n\n  .slider__track\n    background: off\n\n  .slider__track__container    \n    &:after\n      border: 0 solid ticks\n      border-left-width: 2px\n    \n  .slider__thumb\n    border: 4px solid off\n    \n  .slider__thumb--label\n    background: off\n    \n  .slider__ticks\n    background: repeating-linear-gradient(90deg,ticks,ticks 2px,transparent 0,transparent)\n      \n  &.input-group--disabled\n    .slider__thumb\n      background: focus\n      border-color: transparent\n      \n    &.input-group--dirty\n      .slider__track-fill\n        background: off\n  \n  &:not(.input-group--dirty)\n    .slider__thumb-container--label\n      .slider__thumb\n        background: thumb\n  ","/** Theme */\n.input-group--slider\n  &.input-group--light\n    slider-theme($slider-light-off,\n      $slider-light-focus-off,\n      $input-group-light-focus,\n      $slider-light-label,\n      $slider-light-ticks\n    )\n    \n  &.input-group--dark\n    slider-theme($slider-dark-off,\n      $slider-dark-focus-off,\n      $input-group-dark-focus,\n      $slider-dark-label,\n      $slider-dark-ticks\n    )\n\n/** Input Group */\n.input-group--slider\n  flex-direction: row\n  flex-wrap: wrap\n  \n  .input-group__details:before, .input-group__details:after\n    display: none\n    \n  .input-group__input\n    flex: 1 1 100%\n    \n\n  label\n    + .input-group__input\n      margin-left: 16px\n      flex: 1 1 auto\n\n  &.input-group--active\n    .slider__thumb\n      transform: translateY(-50%) scale(1)\n      \n    .slider__track\n      transition: none\n      \n    .slider__thumb-container\n      &--label\n        .slider__thumb\n          transform: translateY(-50%) scale(0)\n          \n          &:hover\n            transform: translateY(-50%) scale(0)\n\n    .slider__thumb-container, .slider__track-fill\n      transition: none\n\n  &.input-group--dirty\n    .slider__thumb\n      background: $theme.primary\n      border-color: $theme.primary\n      \n    .slider__thumb--label\n      background: $theme.primary\n      \n  &.input-group--disabled\n    pointer-events: none\n    \n    .slider__thumb\n      transform: translateY(-50%) scale(.5)\n      background: transparent\n      \n    &.input-group--dirty\n      border-color: transparent\n      \n  &.input-group--prepend-icon\n    .slider\n      margin-left: 56px\n\n\n/** Slider */\n.slider\n  cursor: default\n  display: flex\n  align-items: center\n  position: relative\n  height: 30px\n  flex: 1\n  user-select: none\n\n\n/** Types */\n.input-group\n  &--ticks:hover\n    .slider__track__container:after, .slider__ticks\n      opacity: 1\n\n\n/** Thumb/Track/Ticks */\n.slider\n  &__track__container\n    position: absolute\n    top: 50%\n    transform: translateY(-50%)\n    height: 2px\n    width: 100%\n    overflow: hidden\n    \n    &:after\n      content: ''\n      position: absolute\n      right: 0\n      top: 0\n      height: 2px\n      transition: .3s ease-in-out\n      width: 2px\n      opacity: 0\n    \n  &__track, &__thumb, &__ticks\n    position: absolute\n    top: 0\n    \n  &__track\n    height: 2px\n    left: 0\n    transition: .3s ease-in-out\n    transform-origin: right\n    overflow: hidden\n    width: 100%\n    \n    &-fill\n      position: absolute\n      left: 0\n      height: 2px\n      background: $theme.primary\n      transform-origin: left\n      width: 100%\n      transition: .3s ease-in-out\n    \n  &__ticks, \n  &__ticks-container\n    position: absolute\n    left: 0\n    height: 2px\n    width: 100%\n    \n  &__ticks-container\n    top: 50%\n    overflow: hidden\n    \n  &__ticks\n    transition: .3s ease-in-out\n    opacity: 0\n    \n  &__thumb-container\n    position: absolute\n    top: 50%\n    transition: .3s ease-in-out\n    \n  &__thumb\n    width: 20px\n    height: 20px\n    left: -10px\n    top: 50%\n    border-radius: 50%\n    background: transparent\n    transition: .3s ease-in-out\n    transform: translateY(-50%) scale(.8)\n    user-select: none\n    \n    &:hover\n      transform: translateY(-50%) scale(1)\n\n\n/** Thumb Label */\n.slider\n  &__thumb--label__container\n    position: absolute\n    left: 0\n    top: 0\n    transition: .3s ease-in-out      \n    \n  &__thumb--label\n    display: flex\n    align-items: center\n    justify-content: center\n    font-size: 12px\n    color: #fff\n    width: 28px\n    height: 28px\n    border-radius: 50% 50% 0\n    position: absolute\n    left: -14px\n    top: -40px\n    transform: rotate(45deg)\n    user-select: none\n    transition: .3s ease-in-out\n    \n    span\n      transform: rotate(-45deg) translateZ(0)",".small-dialog\n  display: block\n  height: 100%\n\n  &__content\n    padding: 0 24px\n\n  &__actions\n    text-align: right\n\n  a\n    display: flex\n    align-items: center\n    color: rgba($material-theme.fg-color, $material-theme.primary-text-percent)\n    height: 100%\n    text-decoration: none\n\n    > *\n      width: 100%\n\n  .menu__activator\n    height: 100%\n",".snack\n  background-color: $snackbar-background-color\n  position: fixed\n  display: flex\n  height: 0\n  pointer-events: none\n  z-index: 1000\n  visibility: visible\n\n  &--absolute\n    position: absolute\n\n  &--top\n    top: 0\n    left: 50%\n    transform: translate3d(-50%, 0, 0) translateZ(0)\n\n  &--bottom\n    bottom: 48px\n    left: 50%\n    transform: translate3d(-50%, 0, 0) translateZ(0)\n\n  &--left\n    left: 8px\n    right: initial\n    transform: none\n\n    &.snack--top\n      top: 8px\n\n    &.snack--bottom\n      bottom: 56px\n\n  &--right\n    left: initial\n    right: 8px\n    transform: none\n\n    &.snack--top\n      top: 8px\n\n    &.snack--bottom\n      top: initial\n      bottom: 56px\n\n  &__content\n    background-color: inherit\n    padding: 14px 24px\n    border-radius: 2px\n    pointer-events: auto\n    max-width: 568px\n    min-width: 288px\n    height: 48px\n    align-items: center\n    color: #fff\n    display: flex\n    font-size: 14px\n    justify-content: space-between\n    transition: .4s $transition.swing\n    position: relative !important\n    elevation(6)\n\n    .btn\n      margin: 0 0 0 48px\n\n@media $display-breakpoints.xs-only\n  .snack\n    width: 100%\n    left: 0\n    right: initial\n    transform: none\n\n    &--right, &--left\n      &.snack--top\n        top: 0\n\n      &.snack--bottom\n        bottom: 48px\n\n    &__content\n      border-radius: 0\n      max-width: 100%\n      width: 100%\n\n      .btn\n        margin: 0 0 0 24px\n\n    &--multi-line &__content\n      height: 80px\n      padding: 24px\n\n    &--bottom, &--right\n      &.snack--multi-line\n        bottom: 80px\n\n    &--vertical &__content\n      height: 112px\n      padding: 24px 24px 14px\n      flex-direction: column\n      align-items: initial\n\n      .btn\n        align-self: flex-end\n\n    &--bottom, &--right\n      &.snack--vertical\n        bottom: 112px\n",".stepper\n  overflow: hidden\n  position: relative\n  elevation(2)\n\n.stepper\n  &__header\n    align-items: stretch\n    display: flex\n    flex-wrap: wrap\n    justify-content: space-between\n    elevation(2)\n\n    .divider\n      align-self: center\n      margin: 0 -16px\n\n\n.stepper\n  &__step__step\n    align-items: center\n    background: rgba($material-theme.fg-color, $material-theme.disabledORhints-text-percent)\n    border-radius: 50%\n    color: $material-theme.bg-color\n    display: flex\n    font-size: 12px\n    justify-content: center\n    height: 24px\n    margin-right: 8px\n    width: 24px\n    transition: .3s $transition.fast-in-fast-out\n\n    .icon\n      font-size: 18px\n      color: rgba($material-theme.bg-color)\n\n.stepper\n  &__step\n    align-items: center\n    display: flex\n    flex-direction: row\n    flex-wrap: wrap\n    padding: 24px\n    position: relative\n\n    &--active\n      .stepper__label\n        text-shadow: 0px 0px 0px black\n        transition: .3s $transition.ease-in-out\n\n      .stepper__step__step\n        background: $theme.primary\n        // font-size: 12px\n\n    &--editable\n      cursor: pointer\n\n      &:hover\n        background: rgba($material-theme.fg-color, .06)\n        text-shadow: 0px 0px 0px black\n\n    &--inactive\n      .stepper__label\n        color: rgba($material-theme.fg-color, $material-theme.disabledORhints-text-percent)\n\n        small\n          color: rgba($material-theme.fg-color, $material-theme.disabledORhints-text-percent)\n\n      .stepper__step__step:not(&.stepper__step--error)\n        color: $material-theme.bg-color\n        background: rgba($material-theme.fg-color, $material-theme.disabledORhints-text-percent)\n\n        &:hover\n          background: rgba(#000, .50)\n\n      &.stepper__step--editable\n        &:hover\n          .stepper__step__step\n            background: rgba(#000, .5)\n\n    &--error\n      .stepper__step__step\n        background: transparent\n        color: $theme.error\n\n        .icon\n          font-size: 24px\n          color: $theme.error\n\n      .stepper__label\n        color: $theme.error\n        text-shadow: none\n        font-weight: 500\n\n        small\n          color: $theme.error\n\n    &--complete\n      .stepper__label\n        color: rgba($material-theme.fg-color, $material-theme.primary-text-percent)\n\n      .stepper__step__step\n        background: $theme.primary\n\n.stepper\n  &__label\n    align-items: flex-start\n    display: flex\n    flex-direction: column\n    text-align: left\n    color: rgba($material-theme.fg-color, $material-theme.disabledORhints-text-percent)\n\n    small\n      font-size: 12px\n      font-weight: 300\n      color: rgba($material-theme.fg-color, $material-theme.secondary-text-percent)\n      text-shadow: none\n\n.stepper\n  &__wrapper\n    overflow: hidden\n    transition: .4s $transition.ease-in-out\n\n.stepper\n  &__content\n    top: initial\n    bottom: 0\n    padding: 16px\n    transition: .4s $transition.ease-in-out\n    flex: 1\n    // Chrome has an issue with overflow hidden for rendering\n    // Originally used translateZ but this messes up fixed\n    // elements within the stepper\n    // Fix for #512 and #620\n    // overflow: hidden\n    width: 100%\n\n    .btn\n      margin-left: 0\n\n.stepper\n  &--non-linear\n    .stepper__step:not(.stepper__step--complete)\n      .stepper__label\n        color: rgba($material-theme.fg-color, $material-theme.secondary-text-percent)\n\n  &--vertical\n    padding-bottom: 36px\n\n    .stepper__content\n      margin: -8px 0 -16px 36px\n      padding: 16px 60px 16px 23px\n      width: auto\n\n    .stepper__content:not(:last-child)\n      border-left: 1px solid $material-twelve-percent-dark\n\n    .stepper__step\n      padding: 24px 24px 16px\n\n    .stepper__step__step\n      margin-right: 12px\n\n  &--alt-labels\n    .stepper__header\n      .divider\n        margin: 35px -67px 0\n        align-self: flex-start\n\n    .stepper__step\n      flex-direction: column\n      justify-content: flex-start\n      align-items: center\n      flex-basis: 175px\n\n      small\n        align-self: center\n\n    .stepper__step__step\n      margin-right: 0\n      margin-bottom: 12px\n\n@media $display-breakpoints.sm-and-down\n  .stepper:not(.stepper--vertical)\n    .stepper__label\n      display: none\n\n    .stepper__step__step\n      margin-right: 0\n\n",".subheader\n  height: $list-item-single-height\n  display: flex\n  align-items: center\n  font-size: $subheader-height-font-size\n  font-weight: $subheader-font-weight\n  padding: 0 $list-right-padding 0 $list-left-padding\n\n  &--inset\n    margin-left: $subheader-inset-margin\n\n  &--dark\n    color: rgba($material-light.fg-color, $material-light.secondary-text-percent)\n\n  &--light\n    color: rgba($material-dark.fg-color, $material-light.secondary-text-percent)\n\n.application--dark\n  .subheader:not(.subheader--dark):not(.subheader--light)\n    color: rgba($material-dark.fg-color, $material-dark.secondary-text-percent)\n\n.application--light\n  .subheader:not(.subheader--dark):not(.subheader--light)\n    color: rgba($material-light.fg-color, $material-light.secondary-text-percent)\n",".table\n  &__overflow\n    width: 100%\n    overflow-x: auto\n    overflow-y: hidden\n\ntable.table\n  border-radius: 2px\n  border-collapse: collapse\n  border-spacing: 0\n  width: 100%\n  max-width: 100%\n\n  tr:not(:last-child)\n    border-bottom: 1px solid rgba($material-theme.fg-color, $material-theme.divider-percent)\n\n  thead, tbody\n    td, th\n      &:not(:nth-child(1)), &:first-child\n        padding: 0 24px\n\n  thead\n    tr\n      height: 56px\n\n    th\n      color: rgba($material-theme.text-color, $material-theme.secondary-text-percent)\n      font-weight: 600\n      font-size: 12px\n      transition: .3s $transition.swing\n      white-space: nowrap\n      user-select: none\n\n      &.sortable\n        pointer-events: auto\n\n      > div\n        width: 100%\n\n  tbody\n    tr\n      transition: background $primary-transition\n      will-change: background\n\n      &[active]\n        background: #f5f5f5\n\n      &:hover\n        background: rgba($material-theme.fg-color, $material-theme.divider-percent)\n\n    td, th\n      height: 48px\n\n    td\n      font-weight: 500\n      font-size: 13px\n\n  .input-group--selection-controls\n    margin: 0\n\n    .input-group__details\n      display: none\n\n    &.checkbox\n      .icon\n        left: 50%\n        transform: translateX(-50%)\n\n      .input-group--selection-controls__ripple\n        left: 50%\n        transform: translate3d(-50%, -50%, 0)\n  tfoot\n    tr\n      height: 56px\n      border-top: 1px solid rgba($material-theme.fg-color, $material-theme.divider-percent)\n",".tabs\n  overflow: hidden\n  position: relative\n  width: 100%\n\n  > .card\n    border-radius: 0\n\n  &--grow\n    .tabs__container\n      > li\n        flex-grow: 1\n\n  &--centered\n    .tabs__container\n      > li:first-of-type\n        margin-left: auto\n      > li:last-of-type\n        margin-right: auto\n\n  &--icons\n    .tabs__bar\n      height: 90px\n\n  &--scroll-bars\n    .tabs__bar--mobile\n      padding: 0 35px\n\n      .icon--left, .icon--right\n        display: flex\n\n      .tabs__container\n        width: calc(100% - 70px)\n\n\n  &__bar\n    background-color: $theme.primary\n    width: 100%\n    position: relative\n    height: 60px\n\n    .icon--left, .icon--right\n      position: absolute\n      top: 0\n      width: 35px\n      display: none\n      align-items: center\n      height: 100%\n      cursor: pointer\n      user-select: none\n\n      ^[0]--dark &\n        color: $color-dark\n\n      ^[0]--light &\n        color: $color-light\n\n    .icon--left\n      left: 0\n\n    .icon--right\n      right: 0\n\n  &__container\n    overflow-x: auto\n    overflow-y: hidden\n    display: flex\n    height: 100%\n    width: 100%\n    position: absolute\n    padding: 0\n    top: 0\n    align-items: center\n    list-style: none\n\n    > li:not(.tabs__slider)\n      height: 100%\n\n    &-left\n      position: absolute\n      left: 0\n      top: 0\n      height: 100%\n      width: 35px\n      display: flex\n      align-items: center\n\n  &__item\n    display: flex\n    flex-shrink: 0\n    flex-direction: column\n    align-items: center\n    justify-content: center\n    min-width: 0\n    height: 100%\n    padding: 1rem\n    position: relative\n    text-align: center\n    text-decoration: none\n    text-transform: uppercase\n    text-overflow: ellipsis\n    transition: $primary-transition\n    white-space: nowrap\n\n    ^[0]--dark ^[-2..-1]\n      color: rgba($material-light.fg-color, $material-light.inactive-icon-percent)\n\n      &--active\n        color: rgba($material-light.fg-color, $material-light.active-icon-percent)\n\n    ^[0]--light ^[-2..-1]\n      color: rgba($material-dark.fg-color, $material-dark.inactive-icon-percent)\n\n      &--active\n        color: rgba($material-dark.fg-color, $material-dark.active-icon-percent)\n\n    .icon\n      color: inherit\n      flex: 1\n      flex-basis: 100%\n      font-size: 32px\n      margin: .5rem 0\n\n    &--disabled\n      pointer-events: none\n\n  &__items\n    position: relative\n    border-width: 0 1px 1px 1px\n    border-style: solid\n    border-color: rgba($material-theme.fg-color, .1)\n\n  &__content\n    transition: $primary-transition\n    width: 100%\n\n  &__slider\n    position: absolute\n    bottom: 0\n    height: 4px\n    background: $theme.accent\n    transition: $primary-transition\n","/** Label */\n.input-group--text-field label\n  pointer-events: none\n  position: absolute\n  top: 0\n  left: 0\n  min-width: 0\n  white-space: nowrap\n  text-overflow: ellipsis\n  overflow: hidden\n  text-align: left\n  transform: translate3d(0, 0, 0)\n  transform-origin: top left\n  width: 100%\n\n.input-group--text-field.input-group--multi-line:not(.input-group--focused):not(.input-group--dirty) label\n  transform: translate3d(0,0,0)\n\n\n/** Input */\n.input-group--text-field input\n  font-size: $input-font-size\n  flex: 1\n  margin: 0\n  white-space: nowrap\n  text-overflow: ellipsis\n  overflow: hidden\n  min-width: 0\n  height: $input-height\n\n  + .icon\n    padding: 0 6px\n    transition: .3s $transition.ease-in-out\n\n  &:focus\n    outline: none\n\n  &:disabled\n    pointer-events: none\n\n/** Textarea */\n.input-group--text-field textarea\n  font-size: $input-font-size\n  flex: 1 1\n\n  &:focus\n    outline: none\n\n/** Counter */\n.input-group--text-field\n  .input-group__counter\n    margin-left: auto\n\n    &--error\n      color: $theme.error !important\n\n/** Themes */\n.input-group--text-fie